import React from "react";
import {
  Box,
  Card,
  CardContent,
  Typography,
  Grid,
  LinearProgress,
  CircularProgress,
} from "@mui/material";

export default function MainContent() {
  return (
    <Box component="main" flex={1} p={4} bgcolor="#f7f7f7" overflow="auto">
      {/* Dashboard Header */}
      <Card sx={{ mb: 4 }}>
        <CardContent>
          <Typography variant="h4" fontWeight="bold" gutterBottom>
            Welcome Back, Victor!
          </Typography>
          <Typography variant="body1" color="textSecondary">
            Here is a summary of your storeâ€™s performance. Keep track of key metrics and trends
            to ensure success.
          </Typography>
        </CardContent>
      </Card>

      {/* Key Metrics Section */}
      <Grid container spacing={3} sx={{ mb: 4 }}>
        {[{
          title: "Total Revenue",
          value: "$124,567",
          percentage: "+15%",
          color: "primary",
        },
        {
          title: "Total Sales",
          value: "3,452",
          percentage: "+10%",
          color: "success",
        },
        {
          title: "Average Order Value",
          value: "$120",
          percentage: "+5%",
          color: "secondary",
        },
        {
          title: "Pending Orders",
          value: "47",
          percentage: "-3%",
          color: "warning",
        }].map(({ title, value, percentage, color }, index) => (
          <Grid item xs={12} sm={6} md={3} key={index}>
            <Card sx={{ textAlign: "center", py: 3 }}>
              <Typography variant="subtitle1" color="textSecondary">
                {title}
              </Typography>
              <Typography variant="h5" fontWeight="bold" gutterBottom>
                {value}
              </Typography>
              <Typography variant="caption" color={`${color}.main`}>
                {percentage}
              </Typography>
            </Card>
          </Grid>
        ))}
      </Grid>

      {/* Sales Overview Section */}
      <Card sx={{ mb: 4 }}>
        <CardContent>
          <Typography variant="h5" fontWeight="bold" gutterBottom>
            Sales Overview
          </Typography>
          <Typography variant="body2" color="textSecondary" gutterBottom>
            Analyze weekly, monthly, and yearly sales trends to understand your growth trajectory.
          </Typography>
          <Box
            sx={{
              height: 300,
              bgcolor: "#f5f5f5",
              borderRadius: 1,
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              color: "text.secondary",
            }}
          >
            {/* Replace this placeholder with a Bar Chart */}
            Bar Chart Placeholder
          </Box>
        </CardContent>
      </Card>

      {/* Revenue Update Section */}
      <Card sx={{ mb: 4 }}>
        <CardContent>
          <Typography variant="h5" fontWeight="bold" gutterBottom>
            Revenue Update
          </Typography>
          <Grid container spacing={3}>
            <Grid item xs={12} md={6}>
              <Box
                sx={{
                  height: 300,
                  bgcolor: "#f5f5f5",
                  borderRadius: 1,
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                  color: "text.secondary",
                }}
              >
                {/* Replace this placeholder with a Pie Chart */}
                Pie Chart Placeholder
              </Box>
            </Grid>
            <Grid item xs={12} md={6}>
              {[{
                label: "Online Sales",
                value: "$80,000",
                percentage: "65% of total",
              },
              {
                label: "In-Store Sales",
                value: "$44,567",
                percentage: "35% of total",
              }].map(({ label, value, percentage }, index) => (
                <Card sx={{ mb: 2 }} key={index}>
                  <CardContent>
                    <Typography variant="subtitle1" fontWeight="bold">
                      {label}
                    </Typography>
                    <Typography variant="body2" color="textSecondary">
                      {percentage}
                    </Typography>
                    <Typography variant="h6" fontWeight="bold" color="textPrimary">
                      {value}
                    </Typography>
                  </CardContent>
                </Card>
              ))}
            </Grid>
          </Grid>
        </CardContent>
      </Card>

      {/* Payment Gateways Section */}
      <Card sx={{ mb: 4 }}>
        <CardContent>
          <Typography variant="h5" fontWeight="bold" gutterBottom>
            Payment Gateways
          </Typography>
          <Typography variant="body2" color="textSecondary" gutterBottom>
            Breakdown of payment methods used by customers.
          </Typography>
          <Grid container spacing={3}>
            {[{
              method: "Credit Card",
              percentage: "55%",
              color: "primary",
            },
            {
              method: "PayPal",
              percentage: "30%",
              color: "success",
            },
            {
              method: "Bank Transfer",
              percentage: "10%",
              color: "warning",
            },
            {
              method: "Other",
              percentage: "5%",
              color: "secondary",
            }].map(({ method, percentage, color }, index) => (
              <Grid item xs={12} sm={6} key={index}>
                <Card>
                  <CardContent sx={{ display: "flex", justifyContent: "space-between" }}>
                    <Typography variant="subtitle1" color={`${color}.main`}>
                      {method}
                    </Typography>
                    <Typography variant="h6" fontWeight="bold">
                      {percentage}
                    </Typography>
                  </CardContent>
                </Card>
              </Grid>
            ))}
          </Grid>
        </CardContent>
      </Card>

      {/* Analytics Section */}
      <Card>
        <CardContent>
          <Typography variant="h5" fontWeight="bold" gutterBottom>
            Analytics
          </Typography>
          <Grid container spacing={3}>
            <Grid item xs={12} md={6}>
              <Box
                sx={{
                  height: 300,
                  bgcolor: "#f5f5f5",
                  borderRadius: 1,
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                  color: "text.secondary",
                }}
              >
                {/* Replace this placeholder with a Gauge Chart */}
                Gauge Chart Placeholder
              </Box>
            </Grid>
            <Grid item xs={12} md={6}>
              <Box
                sx={{
                  height: 300,
                  bgcolor: "#f5f5f5",
                  borderRadius: 1,
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                  color: "text.secondary",
                }}
              >
                {/* Replace this placeholder with a Traffic Source Chart */}
                Traffic Source Chart Placeholder
              </Box>
            </Grid>
          </Grid>
        </CardContent>
      </Card>
    </Box>
  );
}